import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Countdown } from "components";

import ResultMain from "../components/Result";
import History from "../components/History";
import RatingWinner from "../components/RatingWinner";
import Notice from "../components/Notice";
import "./style.scss";
import moment from "moment";
// import DateCountdown from "react-date-countdown-timer";
const PowerBallConentMain = ({
  isMobile,
  isNotMain,
  listResult,
  getListRsultGamePowerball,
  reLoadDataGame,
  roundofgame,
  endRoundOfGame,
  roundofdate,

  //lotto
  listResultLotto,
  roundofgameLotto,
  endRoundOfGameLotto,
  getListRsultGameLotto,
  reLoadDataGameLotto,
}) => {
  const type = "POWERBALL";
  const pageSize = 50;
  const [timer, setTimer] = useState(new Date().getTime() + 15000);

  useEffect(() => {
    onReloadData();
  }, []);
  const onReloadData = () => {
    getListRsultGamePowerball({ pageSize: pageSize, gametypeid: 3 });
    reLoadDataGame({ type: type, gametypeid: 3, typeGame: 1000005 });
  };

  const onReloadDataLottoMobile = () => {
    getListRsultGameLotto({ pageSize: pageSize });
    reLoadDataGameLotto({ type: "LOTTO" });
  };
  {
    console.log("endRoundOfGame: ", endRoundOfGame);
  }
  return (
    <div className={`pb-${isMobile || isNotMain ? 0 : 5}`}>
      <ResultMain
        type={type}
        data={listResult}
        roundofgame={roundofgame}
        onReloadData={onReloadData}
        endRoundOfGame={endRoundOfGame}
        roundofdate={roundofdate}
      />

      {(isMobile && (
        <ResultMain
          type="LOTTO"
          data={listResultLotto}
          roundofgame={roundofgameLotto}
          endRoundOfGame={endRoundOfGameLotto}
          onReloadData={onReloadDataLottoMobile}
        />
      )) ||
        null}
      {!isNotMain ? isMobile && <History /> : null}
      {(!isNotMain && <RatingWinner />) || null}
      {(!isNotMain && <Notice />) || null}
    </div>
  );
};

PowerBallConentMain.propTypes = {
  isMobile: PropTypes.bool,
  listResult: PropTypes.array,
  getListRsultGamePowerball: PropTypes.func,
  reLoadDataGamePowerball: PropTypes.func,
  onGetInfoGame: PropTypes.func,
  roundofgame: PropTypes.number,
  endRoundOfGame: PropTypes.string,

  //lotto
  listResultLotto: PropTypes.array,
  roundofgameLotto: PropTypes.number,
  endRoundOfGameLotto: PropTypes.string,
  getListRsultGameLotto: PropTypes.func,
  reLoadDataGameLotto: PropTypes.func,
};

export default connect(
  (state) => {
    const { powerball, lotto } = state;
    return {
      listResult: powerball?.listResult || [],
      roundofgame: powerball?.infoGame?.roundofgame || 0,
      roundofdate: powerball?.infoGame?.roundofdate || 0,
      endRoundOfGame: powerball?.endRoundOfGame,

      //Lotto show for mobile
      listResultLotto: lotto?.listResult || [],
      roundofgameLotto: lotto?.infoGame?.roundofgame || 0,
      endRoundOfGameLotto: lotto?.endRoundOfGame,
    };
  },
  ({ powerball: { getListRsultGame, reLoadDataGame }, lotto }) => {
    return {
      getListRsultGamePowerball: getListRsultGame,
      reLoadDataGame: reLoadDataGame,
      //lotto
      getListRsultGameLotto: lotto?.getListRsultGame,
      reLoadDataGameLotto: lotto?.reLoadDataGame,
    };
  }
)(PowerBallConentMain);
