import React, { useState, useEffect, useRef } from "react";
import PropTypes from "prop-types";
import Carousel from "react-multi-carousel";
import { isArray } from "lodash";

import { renderTextDate } from "helper/game";
import { BoxLottery, ItemBall, Countdown } from "components";
import "./style.scss";

const responsive = {
  superLargeDesktop: {
    // the naming can be any, depends on you.
    breakpoint: { max: 4000, min: 3000 },
    items: 1,
  },
  desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 1,
  },
  tablet: {
    breakpoint: { max: 1024, min: 464 },
    items: 1,
  },
  mobile: {
    breakpoint: { max: 464, min: 0 },
    items: 1,
  },
};

const ResultMain = ({
  type,
  data,
  onReloadData,
  roundofgame,
  endRoundOfGame,
  roundofdate,
}) => {
  const refCarousel = useRef(null);
  const renderImage = type !== "LOTTO" ? "Logo_game_pow" : "loto-tran";
  const [acResult, SetAcResult] = useState({});
  const [count, SetCount] = useState(0);

  useEffect(() => {
    if (isArray(data) && data.length) {
      SetAcResult(data[count]);
    }
  }, [data, count]);
  const showCarousel = (data || []).map((_i, i) => (
    <div
      className="flex-center justify-between flex-wrap pd-2 pl-3 pr-3"
      key={String(`____key-items-result-${i}-${type}`)}
    >
      {(_i?.data || []).map((item, index) => {
        if (index + 1 == _i?.totalBall) {
          return (
            <>
              <span className="plus-ic mr-1">+</span>
              <ItemBall
                value={item}
                key={String(`_____key-item-${index + 1}`)}
              />
            </>
          );
        }
        return (
          <span>
            <ItemBall value={item} key={String(`_____key-item-${index}`)} />
          </span>
        );
      })}
    </div>
  ));
  const nextItem = () => {
    if (isArray(data) && data.length) {
      let a = count - 1;
      // const leg = data.length - 1;//for loop
      // if (a < 0) a = leg;//for loop
      if (a <= 0) a = 0;

      SetCount(a);
    }
    refCarousel.current.previous();
  };
  const prevItem = () => {
    if (isArray(data) && data.length) {
      let a = count + 1;
      const leg = data.length - 1;
      // if (a > leg) a = 0;//for loop
      if (a > leg) a = leg;

      SetCount(a);
    }
    refCarousel.current.next();
  };
  const _renderTextTitle = () => {
    if (type == "LOTTO") return `${acResult?.drawno || ""}회 당첨결과`;

    return `${acResult?.roundofdate || ""}(${
      acResult?.drawno || ""
    })회 당첨결과`;
  };
  const _renderTextCountDown = () => {
    if (type == "LOTTO") return `${roundofgame || 0}회차 추첨까지`;

    return `${roundofdate || ""}(${roundofgame || 0})회차 추첨까지`;
  };
  return (
    <div className={`pb-2 result-main ${type}`}>
      <BoxLottery style={{ width: "100%", display: "flex" }} span={24}>
        <div className="pd-7 content">
          <div className="info d-flex align-end">
            <img src={require(`resources/images/${renderImage}.png`)} />
            <div className="ml-2 text">
              <p className="m-0 color-000 f-16 title">{_renderTextTitle()}</p>
              <p className="m-0 color-000 f-12">
                {renderTextDate(acResult?.createdat || new Date())}
              </p>
            </div>
          </div>
          <div className="pt-4 result">
            <Carousel
              ref={refCarousel}
              arrows={false}
              responsive={responsive}
              swipeable={false}
              draggable={false}
              ssr={true} // means to render carousel on server-side.
              // infinite={true}
              keyBoardControl={true}
              containerClass="carousel-container"
            >
              {showCarousel}
            </Carousel>
            <img
              onClick={nextItem}
              className="icon-next ic"
              src={require(`resources/images/icon/${
                type !== "LOTTO" ? "ic_next" : "ic_next_FFF"
              }.png`)}
            />
            <img
              onClick={prevItem}
              className="icon-prev ic"
              src={require(`resources/images/icon/${
                type !== "LOTTO" ? "ic_prev" : "ic_prev_FFF"
              }.png`)}
            />
          </div>
          <div className="flex-center">
            <span>{_renderTextCountDown()}</span>
            {(endRoundOfGame && (
              <Countdown
                dateTime={endRoundOfGame}
                callback={onReloadData}
                type={type}
              />
            )) ||
              null}
          </div>
        </div>
      </BoxLottery>
    </div>
  );
};

ResultMain.propTypes = {
  type: PropTypes.string,
  endRoundOfGame: PropTypes.string,
  data: PropTypes.array,
  onReload: PropTypes.func,
  roundofgame: PropTypes.number,
  roundofdate: PropTypes.number,
};

export default ResultMain;
