import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Spin, Empty } from "antd";

import { BoxLottery, ItemText, TitleBox } from "components";
import "./style.scss";

const RatingWinner = ({ winners, getBallHistoryWinners }) => {
  const [loading, SetLoading] = useState(false);
  useEffect(() => {
    mapDataShow();
  }, []);

  const mapDataShow = async () => {
    SetLoading(true);

    await getBallHistoryWinners().then().catch();
    SetLoading(false);
  };
  return (
    <div className="rating-winner-main pb-2">
      <BoxLottery style={{ width: "100%", display: "flex" }} span={24}>
        <div className="pd-5 content">
          <div className="d-flex title-box">
            <TitleBox name={"당첨 현황"} fontSize={"1rem"} hiddenGach />
            <span className="pl-1 plus-ic mt-1">+</span>
          </div>

          <div className="list-item" id="scroll">
            {winners.map((item, ind) => (
              <ItemText
                key={`____key-list-item-main-${ind}`}
                data={item?.data || []}
              />
            ))}

            <Empty
              image={Empty.PRESENTED_IMAGE_SIMPLE}
              style={{ display: !winners.length ? "block" : "none" }}
            />
            <div
              style={{ display: loading ? "flex" : "none" }}
              className="loading flex-center spin"
            >
              <Spin />
            </div>
          </div>
        </div>
      </BoxLottery>
    </div>
  );
};

RatingWinner.propTypes = {
  getBallHistoryWinners: PropTypes.func,
  winners: PropTypes.array,
};

export default connect(
  (state) => {
    const { main } = state;

    return {
      winners: main?.winners || [],
    };
  },
  ({ main: { getBallHistoryWinners } }) => {
    return { getBallHistoryWinners };
  }
)(RatingWinner);
