import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";

import ResultMain from "../components/Result";
import History from "../components/History";
import ContactUs from "../components/ContactUs";
import "./style.scss";

const LottoConentMain = ({
  isMobile,
  listResult,
  getListRsultGame,
  reLoadDataGame,
  onGetInfoGame,
  roundofgame,
  endRoundOfGame,
  isNotMain,
}) => {
  const type = "LOTTO";
  const pageSize = 50;

  useEffect(() => {
    onReloadData();
  }, []);

  const onReloadData = () => {
    getListRsultGame({ pageSize: pageSize });
    reLoadDataGame({ type: type });
  };
  return (
    <div className={!isNotMain && "pb-5"}>
      {(!isMobile && (
        <ResultMain
          type={type}
          data={listResult}
          onReloadData={onReloadData}
          roundofgame={roundofgame}
          endRoundOfGame={endRoundOfGame}
        />
      )) ||
        null}
      {!isNotMain ? !isMobile && <History /> : null}
      {(!isNotMain && <ContactUs />) || null}
    </div>
  );
};

LottoConentMain.propTypes = {
  isMobile: PropTypes.bool,
  listResult: PropTypes.array,
  getListRsultGame: PropTypes.func,
  reLoadDataGame: PropTypes.func,
  onGetInfoGame: PropTypes.func,
  roundofgame: PropTypes.number,
  endRoundOfGame: PropTypes.string,
};

export default connect(
  (state) => {
    const { lotto } = state;
    return {
      listResult: lotto?.listResult || [],
      roundofgame: lotto?.infoGame?.roundofgame || 0,
      endRoundOfGame: lotto?.infoGame?.endroundofgame,
    };
  },
  ({ lotto: { getListRsultGame, reLoadDataGame, onGetInfoGame } }) => {
    return {
      getListRsultGame,
      reLoadDataGame,
      onGetInfoGame,
    };
  }
)(LottoConentMain);
