import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { Table } from "antd";
import { connect } from "react-redux";
import { useHistory } from "react-router-dom";

import { BoxLottery, TitleBox } from "components";
import "./style.scss";

const COLUNMS_LOTTO = [
  {
    title: "제목",
    dataIndex: "fullcontent",
    key: "1",
    render: (text) => <div className="line-clamp-1">{text}</div>,
    width: "65%",
  },
  {
    title: "일시",
    dataIndex: "timerConvert",
    key: "2",
    width: 125,
  },
];

const Notice = ({ data, onGetDataNotices }) => {
  const history = useHistory();
  const [loading, SetLoading] = useState(false);
  useEffect(() => {
    mapDataShow();
  }, []);

  const mapDataShow = async () => {
    SetLoading(true);

    await onGetDataNotices({ pageIndex: 1, pageSize: 10 }).then().catch();
    SetLoading(false);
  };
  return (
    <div className="notice-main pb-2">
      <BoxLottery style={{ width: "100%", display: "flex" }} span={24}>
        <div className="pd-5 content">
          <div className="d-flex title-box">
            <TitleBox name="당첨규정" fontSize={"1rem"} hiddenGach />
            <span className="pl-1 plus-ic mt-1">+</span>
          </div>

          <Table
            columns={COLUNMS_LOTTO}
            pagination={{ position: [] }}
            dataSource={data}
            scroll={{ y: 181 }}
            loading={loading}
            onRow={(record) => {
              return {
                onClick: (event) => {
                  history.push(`/notice/detail/${record?.id || 0}`);
                },
              };
            }}
          />
        </div>
      </BoxLottery>
    </div>
  );
};

Notice.propTypes = {
  data: PropTypes.array,
  onGetDataNotices: PropTypes.func,
};

export default connect(
  (state) => {
    const { common } = state;

    return {
      data: common?.notices?.rows || [],
    };
  },
  ({ common: { onGetDataNotices } }) => {
    return { onGetDataNotices };
  }
)(Notice);
