import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types';
import { RedoOutlined } from '@ant-design/icons';
import { connect } from 'react-redux';
import { Spin, Empty } from 'antd';


import { BoxLottery, ItemText, TitleBox } from "components";
import './style.scss'



const History = ({
    getHistoryMain,
    dataHistory
}) => {
    const [loading, SetLoading] = useState(false)
    useEffect(() => {
        onGetHistory()
    }, [])

    const onGetHistory = async () => {
        SetLoading(true)

        await getHistoryMain().then().catch()
        SetLoading(false)
    }
    const showRender = dataHistory.map((_i, ind) => <ItemText key={String(ind)} data={_i?.data || []} />)
    return (
        <div className="history-main pb-2">
            <BoxLottery
                style={{ width: '100%', display: 'flex' }}
                span={24}
            >
                <div className="pd-5 content">
                    <div className="flex-center title-box">
                        <TitleBox name={'구매 현황'} hiddenGach />
                        <RedoOutlined className="ic-refesh mb-1" onClick={onGetHistory} />
                    </div>

                    <div className="list-item" id="scroll">
                        {showRender}
                        <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} style={{ display: !dataHistory.length ? 'block' : 'none' }} />
                        <div style={{ display: loading ? 'flex' : 'none' }} className="loading flex-center spin">
                            <Spin />
                        </div>
                    </div>
                </div>
            </BoxLottery>
        </div>
    )
}


History.propTypes = {
    dataHistory: PropTypes.array,
    getHistoryMain: PropTypes.func,
};

export default connect(
    state => {
        const { main = {} } = state;

        return {
            dataHistory: main?.dataHistory || []
        }
    },
    ({ main: { getHistoryMain } }) => {
        return { getHistoryMain }
    }
)(History)
