import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types';
import { RedoOutlined } from '@ant-design/icons';


import { BoxLottery, ItemText, TitleBox } from "components";
import './style.scss'


const data = [
    {
        ic: 'images/icon/ic_phone.png',
        text1: '유선지원',
        text2: '고객센터',
        content: '123456789',
    },
    {
        ic: 'images/icon/ic_tele.png',
        text1: '유선지원',
        text2: '고객센터',
        content: '123456789',
    },
    {
        ic: 'images/icon/ic_talk.png',
        text1: '유선지원',
        text2: '고객센터',
        content: '123456789',
    },

]
const ContactUs = ({
    type
}) => {


    const renderContactUs = data.map((_, i) => (
        <div className="item-contact-us pb-2" key={String(i)}>
            <BoxLottery
                style={{ width: '100%', display: 'flex' }}
                span={24}
            >
                <div className="item pd-5 flex-center justify-between">
                    <img height={18} src={require(`resources/${_?.ic}`)} />
                    <div className="flex-center">
                        <span className="mr-1 bold">{_?.text1 || ''}</span>
                        <span >{_?.text2 || ''}</span>
                    </div>
                    <span className="bold">{_?.content || ''}</span>
                </div>
            </BoxLottery>
        </div>
    ))
    return (
        <div className="contact-us-main ">
            <div className="content">
                {renderContactUs}
            </div>
        </div >
    )
}


ContactUs.propTypes = {
};

export default ContactUs